plate-columns:repeat(12, 1fr);grid-template-rows:repeat(12, 6vh)}.grid-display #planets{grid-column-start:1;grid-column-end:8;grid-row-start:1;justify-self:center;align-self:center;grid-row-end:12}.grid-display .planet-bio{grid-column-start:9;grid-column-end:12;grid-row-end:8;grid-row-start:1;margin-top:5em}.grid-display .viewButtons{grid-column-start:9;grid-column-end:12}.jumboTron-content{display:flex;justify-content:center;align-content:center;height:92vh}.jumboTron-content .jumboTron{display:flex;justify-content:center;flex-direction:column;align-items:center;max-width:60%;margin:0 auto;background-color:rgba(255,255,255,0.7);opacity:.5;padding:3.5em;border-radius:15px;box-shadow:3px 1px 8px #000}.jumboTron-content .jumboTron .btn{margin-top:2em;background-color:#fff;border:transparent;color:#000;padding:1em;text-align:center}.jumboTron-content .jumboTron .btn:hover{background-color:#2d68f0;color:#fff}.jumboTron-content .jumboTron h1{color:#000}

